import React from 'react';
import PropTypes from 'prop-types';
import { ResponsiveBar } from '@nivo/bar';

const BaseChart = props => (
  <ResponsiveBar
    groupMode={props.groupMode}
    data={props.data}
    keys={props.keys}
    indexBy={props.indexBy}
    tooltip={({ id, value, color }) => (
      <strong style={{ color }}>
        {id}: {value}
      </strong>
    )}
    theme={props.theme}
    margin={{
      top: 50,
      right: 130,
      bottom: 50,
      left: 60,
    }}
    padding={0.3}
    colors={{
      scheme: 'accent',
    }}
    defs={[
      {
        id: 'dots',
        type: 'patternDots',
        background: 'inherit',
        color: '#38bcb2',
        size: 4,
        padding: 1,
        stagger: true,
      },
      {
        id: 'lines',
        type: 'patternLines',
        background: 'inherit',
        color: '#eed312',
        rotation: -45,
        lineWidth: 6,
        spacing: 10,
      },
    ]}
    fill={props.fill}
    borderColor={{
      from: 'color',
      modifiers: [['darker', 1.6]],
    }}
    axisTop={null}
    axisRight={null}
    axisBottom={{
      tickSize: 5,
      tickPadding: 5,
      tickRotation: 0,
      legend: props.legend,
      legendPosition: 'middle',
      legendOffset: 42,
    }}
    axisLeft={{
      tickSize: 5,
      tickPadding: 5,
      tickRotation: 0,
      legend: 'Count',
      legendPosition: 'middle',
      legendOffset: -50,
    }}
    labelSkipWidth={12}
    labelSkipHeight={12}
    labelTextColor={{
      from: 'color',
      modifiers: [['darker', 1.6]],
    }}
    legends={[
      {
        dataFrom: 'keys',
        anchor: 'bottom-right',
        direction: 'column',
        justify: false,
        translateX: 120,
        translateY: 0,
        itemsSpacing: 2,
        itemWidth: 100,
        itemHeight: 20,
        itemDirection: 'left-to-right',
        itemOpacity: 0.85,
        symbolSize: 20,
        effects: [
          {
            on: 'hover',
            style: {
              itemOpacity: 1,
            },
          },
        ],
      },
    ]}
    animate
    motionStiffness={90}
    motionDamping={15}
    height={600}
  />
);

BaseChart.propTypes = {
  data: PropTypes.array.isRequired,
  keys: PropTypes.array.isRequired,
  theme: PropTypes.object.isRequired,
  indexBy: PropTypes.string.isRequired,
  legend: PropTypes.string.isRequired,
  fill: PropTypes.array,
  groupMode: PropTypes.string,
};

BaseChart.defaultProps = {
  fill: [],
  groupMode: 'stacked',
};

export default BaseChart;
