/* eslint-disable no-nested-ternary */
/* eslint-disable prettier/prettier */
/* eslint-disable react/prop-types */
/* eslint-disable import/no-extraneous-dependencies */
import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { get, find, map, chain } from 'lodash';
import withHome from '../../actions/withHome';
import { getAnalyticsData } from '../../actions/home';
import { API_URLS, MESSAGES } from '../../constants';
import { CircularProgress, withStyles, Tabs, Tab, Paper } from '../../materialLib';
import { openNotifier } from '../../actions/notifier';
import withNotifier from '../../actions/withNotifier';
import PageWrapper from '../../components/PageWrapper';
import BaseChart from './components/BaseChart';

const styles = {
  progress: {
    margin: `50px auto`,
    textAlign: 'center',
    display: 'block',
  },
};

const Home = (props, context) => {
  const { classes } = props;
  const [loading, setLoading] = useState(false);
  const [tabId, setTabId] = useState(0);

  const handleChange = (event, value) => {
    setTabId(() => value);
  };

  const getResponse = async () => {
    try {
      const response = await context.fetch(`${API_URLS.analyticsColorCodes}?code=1`);
      const data = await response.json();
      await props.getAnalyticsData({ data: get(data, 'resources') });
      if (response.status !== 200) {
        await props.openNotifier({
          openNotify: true,
          message: MESSAGES.errorInFetching,
          variant: 'error',
        });
      }
    } catch (e) {
      await props.openNotifier({
        openNotify: true,
        message: MESSAGES.successFetch,
        variant: 'success',
      });
    }
  };

  useEffect(() => {
    setLoading(() => true);
    getResponse();
    setLoading(() => false);
  }, []);

  const getAllCounts = () => {
    const project = find(get(props.home, 'data.breakDown'), { name: 'SyndeiaRepositoryType' });
    return (
      get(project, 'children.Repositories') ||
      get(props.home, `data.artifacts`) ||
      get(props.home, `data.artifactTypes`) ||
      get(props.home, `data.containers`) ||
      get(props.home, `data.containerTypes`) ||
      get(props.home, `data.relations`) ||
      get(props.home, `data.relationTypes`) ||
      get(props.home, `data.repositories`) ||
      get(props.home, `data.repositoryTypes`)
    );
  };

  const nivoChartData = () => {
    const projects = find(get(props.home, 'data.breakDown'), { name: 'SyndeiaRepositoryType' });
    return [
      {
        domain: 'Artifacts',
        Artifacts: get(props.home, `data.artifacts`),
      },
      {
        domain: 'ArtifactTypes',
        ArtifactTypes: get(props.home, `data.artifactTypes`),
      },
      {
        domain: 'Containers',
        Containers: get(props.home, `data.containers`),
      },
      {
        domain: 'ContainerTypes',
        ContainerTypes: get(props.home, `data.containerTypes`),
      },
      {
        domain: 'Relations',
        Relations: get(props.home, `data.relations`),
      },
      {
        domain: 'RelationTypes',
        RelationTypes: get(props.home, `data.relationTypes`),
      },
      {
        domain: 'Repositories',
        Repositories: get(props.home, `data.repositories`),
      },
      {
        domain: 'RepositoryTypes',
        RepositoryTypes: get(props.home, `data.repositoryTypes`),
      },
      {
        domain: 'Projects',
        Projects: get(projects, 'children.Repositories'),
      },
    ];
  };

  const createRepositoryTypedData = () => map(get(props.home, 'data.breakDown'), data => chain(data).extend({ 
        "Repository Type": get(data, 'name').replace("RepositoryType", ""), 
        "Repositories": get(data, 'children.Repositories'),
        "RepositoriesColor": "hsl(307, 70%, 50%)",
        "Containers": get(data, 'children.Containers'),
        "ContainersColor": "hsl(96, 70%, 50%)",
        "Artifacts": get(data, 'children.Artifacts'),
        "ArtifactsColor": "hsl(252, 70%, 50%)",
      })
      .omit(["name", "sKey", "children"])
      .value())

  const renderChart = () => {
    let defaultChartTemplate = (<BaseChart
      legend="Domains"
      data={nivoChartData()}
      keys={[
        'Artifacts',
        'ArtifactTypes',
        'Containers',
        'ContainerTypes',
        'Relations',
        'RelationTypes',
        'Repositories',
        'RepositoryTypes',
        'Projects',
      ]}
      indexBy="domain"
      theme={{
        tooltip: {
          container: {
            background: '#333',
          },
        },
        axis: {
          ticks: {
            text: {
              fontSize: 14,
            },
          },
          legend: {
            text: {
              fontSize: 16,
            },
          },
          keys: {
            text: {
              fontSize: 16,
            },
          },
        },
      }}
    />)
    if (tabId !== 0) {
      defaultChartTemplate = (<BaseChart
        groupMode="stacked"
        legend="Repository types"
        data={
          createRepositoryTypedData()
        }
        keys={[
          'Repositories',
          'Containers',
          'Artifacts'
        ]}
        indexBy="Repository Type"
        theme={{
          tooltip: {
            container: {
              background: '#333',
            },
          },
          axis: {
            ticks: {
              text: {
                fontSize: 14,
              },
            },
            legend: {
              text: {
                fontSize: 16,
              },
            },
            keys: {
              text: {
                fontSize: 16,
              },
            },
          },
        }}
      />)
    }
    return defaultChartTemplate;
  }

  return (
    <PageWrapper style={{ width: '100%', height: '96vh', border: 'none', boxShadow: 'none', alignItems: 'baseline', margin: 0 }}>
      <h1>Welcome</h1>
      <p>
        Syndeia (Systems LIfecycle Management) is a software environment for
        integrated model-based systems engineering, connecting high-level
        architecture in SysML to PLM, CAD, databases and more.
      </p>
      <div style={{ float: 'right' }}>
        <Paper className={classes.root}>
          <Tabs
            value={tabId}
            onChange={handleChange}
            variant="fullWidth"
            indicatorColor="primary"
            textColor="primary"
          >
            <Tab label="Domains" />
            <Tab label="Repository types" />
          </Tabs>
        </Paper>
      </div>
      {getAllCounts() && !loading ? renderChart() : loading ?
        <CircularProgress className={classes.progress} /> : 
        <p style={{ textAlign: 'center' }}>{MESSAGES.noResults}</p>
      }
    </PageWrapper>
  );
};

Home.propTypes = {
  home: PropTypes.object,
  classes: PropTypes.object.isRequired,
};

Home.defaultProps = {
  home: {},
};

Home.contextTypes = {
  fetch: PropTypes.func.isRequired,
};

export default connect(null, {
  getAnalyticsData,
  openNotifier,
})(withStyles(styles, { theme: true })(withNotifier(withHome(Home))));